
.wrap {
  max-width: 700px;
  margin: 20px auto;
  border-radius: 10px;
  -webkit-box-shadow: 0 10px 70px rgba(0,0,0,0.1);
          box-shadow: 0 10px 70px rgba(0,0,0,0.1);
  padding: 20px;
}
.fade-enter-active {
  -webkit-animation: leftInX 0.5s linear;
          animation: leftInX 0.5s linear;
}
.fade-leave-active {
  -webkit-animation: leftOutX 0.5s linear;
          animation: leftOutX 0.5s linear;
}
@-webkit-keyframes leftInX {
from{opacity:0;
}
to{opacity:1;
}
}
@keyframes leftInX {
from{opacity:0;
}
to{opacity:1;
}
}
@-webkit-keyframes leftOutX {
from{opacity:1;
}
to{opacity:0;
}
}
@keyframes leftOutX {
from{opacity:1;
}
to{opacity:0;
}
}





























































